.show-tooltip {
  cursor: pointer; }

.tooltip-popup {
  position: absolute;
  z-index: 1000;
  max-width: 550px;
  padding: 10px;
  font-size: 14px;
  background: #ffffff;
  color: #555555;
  border: 1px solid #ccc;
  border-radius: 8px;
  box-shadow: 0 0 10px #999999; }
  .tooltip-popup > div:first-child {
    overflow: hidden; }
  .tooltip-popup.short-tip {
    border-radius: 5px;
    max-width: 400px; }
    .tooltip-popup.short-tip .close-button {
      display: none; }
  .tooltip-popup.bottom-tooltip .nub {
    top: -20px;
    border-top-width: 0;
    border-bottom-width: 20px;
    border-color: transparent transparent #ccc transparent; }
  .tooltip-popup.bottom-tooltip .nub:after {
    top: 1px;
    bottom: auto;
    border-top-width: 0;
    border-bottom-width: 20px;
    border-top-color: transparent;
    border-bottom-color: #fff; }
  .tooltip-popup .nub {
    position: absolute;
    width: 0;
    height: 0;
    border: solid 20px;
    left: 0;
    bottom: -20px;
    margin-left: -21px;
    border-color: #ccc transparent transparent transparent;
    border-bottom-width: 0; }
  .tooltip-popup .nub:after {
    position: absolute;
    display: block;
    width: 0;
    height: 0;
    bottom: 1px;
    margin-left: -20px;
    content: '';
    border: 20px solid transparent;
    border-top-color: #fff;
    border-bottom-width: 0; }
  .tooltip-popup .close-button {
    font-size: 18px !important;
    background: transparent;
    width: 44px;
    height: 44px;
    position: absolute;
    top: -22px;
    right: -22px;
    padding: 0;
    margin: 0;
    border: 0;
    border-radius: 20px; }
    .tooltip-popup .close-button .glyph-icon {
      background: #555;
      border-radius: 20px;
      width: 35px;
      height: 35px;
      display: inline-block; }
      .tooltip-popup .close-button .glyph-icon .glyph-close {
        color: #FFFFFF;
        font-size: 18px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%); }
